import {Universe} from "./Universe";
import {shuffle, speak} from "../misc/Utils";
import App from "../components/App";
import {returnToScanner} from "../components/Dialog";
import React from "react";
import Exploration from "./Exploration";


class LandingExploration extends Exploration {
    calculate(mission) {
        let planet = mission.planet;
        return {
            result: <div key="intro">
                <img className={`detailed-planet-image-${planet.size}`} src={planet.image} alt={planet.name} />
                <p>Your team has landed on {planet.name}, it's a {planet.size} planet that is a {planet.type} planet.</p>
            </div>,
            voice: `Prepare for landing`
        }
    }
}

