class AliensFighting extends Exploration {
    calculate(mission) {
        //let planet = mission.planet;
        return {
            result: <div key="intro">
                <p>Your team was attacked by aliens, exhaust your entire team.</p>
            </div>,
            voice: `Danger, hostile aliens!`
        }
    }
}

class AlienShipArrived extends Exploration {
    constructor(ship) {
        super();

        this.ship = ship;
    }

    calculate(mission) {
        let fromLocation = this.ship.location;
        fromLocation.removeBody(this.ship);

        let planet = mission.planet;
        let toLocation = planet.location;
        toLocation.addBody(this.ship);

        mission.possibleExplorations.push(new AliensFighting());
        mission.possibleExplorations.push(new AliensFighting());
        mission.possibleExplorations.push(new AliensFighting());
        shuffle(mission.possibleExplorations);

        return {
            result: <div key="intro">
                <p>An alien ship from the {fromLocation.name} system, warped here (Move the token). It looks like it's sending some kind of pods towards the surface.</p>
            </div>,
            voice: `Alien ship arrived`
        }
    }
}

export class AwayMission {
    constructor(planet) {
        this.planet = planet;
        this.possibleExplorations = [];

        for (let exploration of planet.explorations) {
            this.possibleExplorations.push(exploration);
        }

        for (let i = 0; i < CELESTIAL_SIZE_MULTIPLIERS[planet.size]; i++) {
            this.possibleExplorations.push(EXPLORATION_NOTHING);
        }

        for (let item of this.planet.location.bodies) {
            if (item instanceof AlienShip) {
                for (let i = 0; i < 3; i++) {
                    this.possibleExplorations.push(new AliensFighting());
                }
            }
        }

        for (let neighbouringLocation of this.planet.location.neighbours(Universe.theUniverse)) {
            for (let item of neighbouringLocation.bodies) {
                if (item instanceof AlienShip) {
                    this.possibleExplorations.push(new AlienShipArrived(item));
                }
            }
        }

        shuffle(this.possibleExplorations);
        this.possibleExplorations.unshift(new LandingExploration()); //add first
        console.log(this.possibleExplorations);
    }

    next() {
        console.assert(this.possibleExplorations.length > 0);
        let exploration = this.possibleExplorations.splice(0, 1)[0];
        let {result, voice, buttons} = exploration.onExplorationDialog(this);
        this.planet.markExplored(exploration);

        let isFullyExplored = this.planet.explorations.length === 0;

        if (voice) {
            speak(voice);
        }

        let buttonComponents = buttons.map((buttonDefinition, index) =>
            <button key={index} onClick={() => buttonDefinition.onClick()}>
                {buttonDefinition.name}
            </button>
        );

        if (!isFullyExplored) {
            buttonComponents.push(<button key="next" className="not-important" onClick={() => {
                this.next()
            }}>Explore</button>);
        }

        buttonComponents.push(
            <button key="leave" className="not-important" onClick={() => {
                if (isFullyExplored)
                    speak(`${this.planet.name} is now fully explored`);
                this.return();
            }}>
                Leave
            </button>
        );

        App.COMPONENT.setDialog(
            <div className="dialog">
                <div className="dialog-inner">
                    <div className="dialog-content">
                        {result}
                        <p>{isFullyExplored ? `${this.planet.name} is now fully explored` : null}</p>
                    </div>
                    <div className="dialog-bottom">
                        {buttonComponents}
                    </div>
                </div>
            </div>
        )
    }

    return() {
        returnToScanner();
    }
}