import {chance, getRandomElement, shuffle, speak} from "../misc/Utils";
import {SpaceStationItem} from "./spaceStation";
import {
    CELESTIAL_SIZE_BIG,
    CELESTIAL_SIZE_MEDIUM,
    CELESTIAL_SIZE_MULTIPLIERS,
    CELESTIAL_SIZE_SMALL,
    CELESTIAL_SIZES,
    NEBULAS,
    PLANET_NAMES,
    PLANET_TYPE_IMAGES,
    PLANET_TYPES,
    SECTORS
} from "../data/planetData";
import {Planet} from "../game/Planet";
import {Location, CelestialBody, SimpleItem} from "../game/map";
import asteroids_image from "../data/planets/asteroids.png";
import black_hole_image from "../data/planets/black_hole.png";
import alien_ship from "../data/planets/alien_ship.png";
import {ZoneCreator} from "../game/ZoneCreator";
import {RESOURCE_A, RESOURCE_B, RESOURCE_D} from "../data/resourceData";
import {ResourceExploration} from "./resource";
import {MAP_ENTITY_DATA} from "../data/mapData";
import {ScientistDataLogEntity} from "./scientist";
import {Universe} from "../game/Universe";
import * as Serialisation from "../misc/Serialisation";
import Icon from "../components/Icon";
import {returnToScanner} from "../components/Dialog";
import React from "react";

function createEntities(universe) {
    for (let mapEntityData of MAP_ENTITY_DATA) {
        universe.registerEntity(new Location(mapEntityData));
    }

    universe.registerEntity(new ScientistDataLogEntity());
}

export class AlienShip extends CelestialBody {
    constructor({...config}) {
        super({
            name: "Alien Ship",
            image: alien_ship,
            size: CELESTIAL_SIZE_MEDIUM,
            ...config,
        })
    }

    get actions() {
        return [{name: `???`, action: null}];
    }

}

Serialisation.registerDynamicClass(AlienShip, "AlienShip");

export function createPossibleSourceFilters(universe) {
    let possibleSourceFilters = [];

    PLANET_TYPES.forEach((planetType) => {
        possibleSourceFilters.push((planet) => planet.type === planetType);
    });

    CELESTIAL_SIZES.forEach((size) => {
        possibleSourceFilters.push((planet) => planet.size === size);
    });

    SECTORS.forEach((sector) => {
        possibleSourceFilters.push((planet) => planet.location.sector === sector);
    });

    [null, ...NEBULAS].forEach((nebula) => {
        possibleSourceFilters.push((planet) => planet.location.nebula === nebula);
    });

    console.log("possibleSourceFilters", possibleSourceFilters.length);
    possibleSourceFilters = possibleSourceFilters.filter(sourceFilter => universe.planets.filter(sourceFilter).length > 0);
    console.log("possibleSourceFilters after viability test", possibleSourceFilters.length);

    shuffle(possibleSourceFilters);
    return possibleSourceFilters;
}

export function initialDialog(universe) {
    let spaceStationLocation = universe.entityForId(universe.startingLocationId);
    let spaceStation = spaceStationLocation.bodies[0];

    speak(`Albatross is located in ${spaceStationLocation.name} system, this is where all the players start their journey.`);

    return <div className="dialog">
        <div className="dialog-inner">
            <div className="dialog-content">
                <img className="detailed-planet-image" src={spaceStation.image} alt={spaceStation.name} />
                <h1>{spaceStation.name}</h1>
                <p>Two days ago we completed a construction of this space station on this side of the
                    wormhole that appeared in our solar system, we are ready to explore this part of the galaxy.</p>
                <p>From an ancient data log we have have found out about a race of ancients that used to live around
                    here and crated a device that allows the owner to create a earth like, perfectly habitable planet.</p>
                <p>Your goal is to find that device and activate it, you will probably need alien artifacts <Icon obj={RESOURCE_D} /> to do
                    it.</p>
                <p><em>Albatross is located in {spaceStationLocation.name} system, this is where all the players start their journey.</em></p>
                <p>The first player to explore the station will get a small reward and will reveal what the humanity learned so far about this part of the galaxy.</p>
            </div>
            <div className="dialog-bottom">
                <button onClick={() => returnToScanner()} >Start game</button>
            </div>
        </div>
    </div>
}

export function createDemoUniverse() {
    let universe = new Universe({name: chance.word()});

    Universe.theUniverse = universe;

    createEntities(universe);

    //
    // Place space station
    //
    let spaceStation = new SpaceStationItem();
    let spaceStationLocation = getRandomElement(universe.locations.filter(entity => entity.neighbourIds.size >= 4));
    spaceStationLocation.addBody(spaceStation);
    universe.startingLocationId = spaceStationLocation.id;

    //
    // Planets
    //
    let planetNames = PLANET_NAMES.slice();
    shuffle(planetNames);
    for (let planetType of PLANET_TYPES) {
        for (let planetSize of CELESTIAL_SIZES) {
            for (let i = 0; i < 2; i++) {
                let location = getRandomElement(universe.locations.filter(entity => entity.bodies.length < 3));

                let planet = new Planet({
                    id: `${planetType}${planetSize}${i}`,
                    name: planetNames.pop(),
                    targetSize: CELESTIAL_SIZE_MULTIPLIERS[planetSize],
                    image: PLANET_TYPE_IMAGES[planetType][planetSize],
                    type: planetType,
                    size: planetSize,
                });

                location.addBody(planet);
            }
        }
    }

    //
    // Asteroids
    //
    for (let i = 0; i < 4; i++) {
        let location = getRandomElement(universe.locations.filter(entity => entity.bodies.length < 3));

        let item = new SimpleItem({
            id: `asteroid-${i}`,
            name: "Asteroids",
            image: asteroids_image,
            size: CELESTIAL_SIZE_SMALL,
        });

        location.addBody(item);
    }

    //
    // Black holes
    //
    for (let i = 0; i < 2; i++) {
        let location = getRandomElement(universe.locations.filter(entity => entity.bodies.length < 3));

        let item = new SimpleItem({
            id: `blackhole-${i}`,
            name: "Black Hole",
            image: black_hole_image,
            size: CELESTIAL_SIZE_BIG,
        });

        location.addBody(item);
    }

    //
    // Alien ship
    //
    for (let i = 0; i < 3; i++) {
        let location = getRandomElement(universe.locations.filter(entity => entity.bodies.length < 3));

        let item = new AlienShip({
            id: `alienship-${i}`
        });

        location.addBody(item);
    }

    let creator = new ZoneCreator(universe);

    let possibleSources = createPossibleSourceFilters(universe);

    for (let resource of [RESOURCE_A, RESOURCE_B]) {
        const filter1 = possibleSources.pop();
        const filter2 = possibleSources.pop();

        creator.addExplorationCandidates({
            exploration: new ResourceExploration({resource: resource, count: 1}),
            surfaceMinRatio: 0.1,
            minTotal: 10,
            maxTotal: 20,
            filter: (planet) => filter1(planet) || filter2(planet),
        });
    }

    creator.addExplorationCandidates({
        exploration: new ResourceExploration({resource: RESOURCE_D, count: 1}),
        surfaceMinRatio: 0.1,
        minTotal: 5,
        maxTotal: 10,
        filter: possibleSources.pop(),
    });

    //universe.dataLogs.add(DATA_LOG_BUGS_LOVE_HIGH_GRAVITY);
    //fillRandomizedDataLogs(universe);
    //fillScanningRestrictions(universe);
    //fillDataLogCards(universe);
    //fillZoneCandidatesFromLogs(universe);
    //fillDataLogExplorations(universe);
    creator.fillUniverse();

    Universe.theUniverse = null;

    return universe;
}