import * as Serialisation from "../misc/Serialisation";
import React from "react";
import {Entity} from "./Entity";
import "./map.css";
import {Universe} from "../game/Universe";
import {CELESTIAL_SIZE_MEDIUM} from "../data/planetData";
import App from "../components/App"

export class CelestialBody {
    constructor({id, locationId = null, name, image, size = CELESTIAL_SIZE_MEDIUM}) {
        this.id = id;
        this.locationId = locationId;
        this.name = name;
        this.image = image;
        this.size = size;
    }

    get location() {
        console.assert(this.locationId !== null, this);
        return Universe.theUniverse.entityForId(this.locationId);
    }

    notifyThatWasSelected() {

    }

    renderImage() {
        return <img className={`detailed-planet-image-${this.size}`} src={this.image} alt={this.name} />;
    }


    get actions() {
        return [];
    }
}

export class SimpleItem extends CelestialBody {
    constructor({staticActions = [{name: `???`, action: null}], ...config}) {
        super(config);
        this.staticActions = staticActions;
    }

    get actions() {
        return this.staticActions;
    }
}

Serialisation.registerDynamicClass(SimpleItem, "SimpleItem");

export class Location extends Entity {
    constructor({neighbourIds = new Set(), sector = null, nebula = null, bodies = [], ...config}) {
        super(config);
        this.neighbourIds = neighbourIds;
        this.bodies = bodies;
        this.sector = sector;
        this.nebula = nebula;
    }

    neighbours(universe) {
        let ret = [];
        for (let neighbourId of this.neighbourIds) {
            let neighbour = universe.entityForId(neighbourId);
            console.assert(neighbour !== null, "neighbour !== null");
            ret.push(neighbour);
        }
        return ret;
    }

    removeBody(item) {
        let index = this.bodies.indexOf(item);
        console.assert(index !== -1);
        this.bodies.splice(index, 1);
    }

    addBody(item) {
        item.locationId = this.id;
        this.bodies.push(item);
    }

    onSelected() {
        let neighbours = this.neighbours(Universe.theUniverse).map(neighbour => neighbour.name).join(", ").replaceLast(", ", " and ");

        let bodies = this.bodies.map(body =>
            <div className={`celestial-info size-${body.size}`} key={body.id} >
                <center>{body.renderImage()}</center>

                {body.actions.map(action =>
                    <button
                        key={action.name}
                        onClick={action.action}
                        className={action.action ? "celestial-action" : "celestial-action-disabled"}
                        style={{marginTop: "1rem"}}>
                        {action.name}
                    </button>
                )}
                <br/>
                <br/>
            </div>

        );

        //{this.name} is fully explored, try flying to another location and exploring there.
        App.COMPONENT.setDialog(<div className="dialog-info">
            <div className="dialog-inner">
                <div className="dialog-content star-system-details">
                    <h1> {this.renderImage()} {this.name}</h1>
                    <p>{this.name} system is a system in the {this.sector} sector{this.nebula ? `, in the ${this.nebula} nebula` : `` }. It neighbours with {neighbours}.</p>

                    <p>{bodies.length === 0 ? "No celestial bodies detected" : ""}</p>
                    {bodies}

                    <br style={{clear:"both"}}/>
                </div>
            </div>
        </div>, false);
    }
}

Serialisation.registerDynamicClass(Location, "MapEntity");
